import React from 'react';
import { useDispatch } from 'react-redux';

import { logout } from '../../store';

import View from './view';

const HomeScreen = () => {
  console.log('HomeScreen');

  const dispatch = useDispatch();

  const sidemenu = [
    { text: '대시보드', link: '/dashboard' },
    { text: '사용자 관리', link: '/user' },
    { text: '일정 관리', link: '/schedule' },
    { text: '신고 관리', link: '/report' },
  ];

  const handleLogout = () => {
    dispatch(logout());
  };

  return <View sidemenu={sidemenu} handleLogout={handleLogout} />;
};

export default HomeScreen;
